
<template>
  <div style="position:fixed;top:50px;bottom:0;left:0;right:0;overflow-y:scroll;">
    <!-- 旧房费用结算 -->
    <div id="big_box">
      <p>旧房费用结算</p>
      <div>
        <section id="top">
          <div
            v-for="(center,i) in oldInfo"
            :key="i"
          >{{center}}</div>
        </section>
      </div>
      <table
        v-if="lastInfo"
        style="width: 100%;"
      >
        <tr
          id="center"
          v-if="lastInfo.elecPrice != 0"
        >
          <td>电费</td>
          <td>{{lastInfo.preElecDegree}}</td>
          <td>
            <input
              type="number"
              step="1"
              min="0"
              @keyup="priceTwoNum(ele,'1')"
              placeholder="请输入"
              v-model="ele"
              class="input_box"
            />
          </td>
        </tr>
        <tr
          id="center"
          v-if="lastInfo.waterPrice != 0"
        >
          <td>水费</td>
          <td>{{lastInfo.preWaterDegree}}</td>
          <td>
            <input
              type="number"
              step="1"
              min="0"
              @keyup="priceTwoNum(water,'2')"
              placeholder="请输入"
              v-model="water"
              class="input_box"
            />
          </td>
        </tr>
        <tr
          id="center"
          v-if="lastInfo.gasPrice != 0"
        >
          <td>气费</td>
          <td>{{lastInfo.preGasDegree}}</td>
          <td>
            <input
              type="number"
              step="1"
              min="0"
              @keyup="priceTwoNum(gas,'3')"
              placeholder="请输入"
              v-model="gas"
              class="input_box"
            />
          </td>
        </tr>
      </table>
    </div>
    <!-- 新房费用结算 -->
    <div
      id="big_box"
      style="margin-top:20px;"
    >
      <p>新房费用结算</p>
      <div>
        <section id="top">
          <div
            v-for="(center,i) in newInfo"
            :key="i"
          >{{center}}</div>
        </section>
      </div>
      <table style="width: 100%;">
        <tr
          id="center"
          v-if="lastInfo.elecPrice != 0"
        >
          <td>电费</td>
          <td>
            <input
              type="number"
              step="1"
              min="0"
              @keyup="priceTwoNum(h_ele,'4')"
              placeholder="请输入"
              v-model="h_ele"
              class="input_box"
            />
          </td>
        </tr>
        <tr
          id="center"
          v-if="lastInfo.waterPrice != 0"
        >
          <td>水费</td>
          <td>{{lastInfo.waterPrice}}</td>
          <td>
            <input
              type="number"
              step="1"
              min="0"
              @keyup="priceTwoNum(h_water,'5')"
              placeholder="请输入"
              v-model="h_water"
              class="input_box"
            />
          </td>
        </tr>
        <tr
          id="center"
          v-if="lastInfo.gasPrice != 0"
        >
          <td>气费</td>
          <td>{{lastInfo.gasPrice}}</td>
          <td>
            <input
              type="number"
              step="1"
              min="0"
              @keyup="priceTwoNum(h_gas,'6')"
              placeholder="请输入"
              v-model="h_gas"
              class="input_box"
            />
          </td>
        </tr>
      </table>
    </div>
    <mt-button
      id="button"
      @click="btn_long"
    >确认换房</mt-button>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { PostBusinessOrderroomRoomchange, GetBusinessLifeCostBillLastByorderroom } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            oldInfo: ['费用项目', '上期读数', '结算读数'],
            newInfo: ['费用项目', '价格', '初始读数'],
            lastInfo: {},
            ele: '',
            water: '',
            gas: '',
            h_ele: '',
            h_water: '',
            h_gas: ''
        };
    },
    created() {
        this.$store.commit('setHeader', '水电结算');
        this.longInfo = JSON.parse(localStorage.getItem('long_next'));
        this.getLifeInfo();
    },
    methods: {
        getLifeInfo() {
            GetBusinessLifeCostBillLastByorderroom(this.longInfo.orderRoomId).done((res) => {
                if (res.data) {
                    this.lastInfo = res.data;
                    this.ele = this.lastInfo.preElecDegree;
                    this.water = this.lastInfo.preWaterDegree;
                    this.gas = this.lastInfo.preGasDegree;
                }
            });
        },
        priceTwoNum(value, num) {
            value = value.toString();
            value = value.replace(/[^\d.]/g, ''); //清除“数字”和“.”以外的字符
            value = value.replace(/\.{2,}/g, '.'); //只保留第一个. 清除多余的
            value = value.replace('.', '$#$').replace(/\./g, '').replace('$#$', '.');
            value = value.replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3'); //只能输入两个小数
            if (value.indexOf('.') < 0 && value != '') {
                //以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额
                value = parseFloat(value);
            }
            if (num == '1') {
                this.ele = value;
            } else if (num == '2') {
                this.water = value;
            } else if (num == '3') {
                this.gas = value;
            } else if (num == '4') {
                this.h_ele = value;
            } else if (num == '5') {
                this.h_water = value;
            } else if (num == '6') {
                this.h_gas = value;
            }
        },
        // 确认换房-长租
        btn_long() {
            let data = {
                orderId: this.longInfo.orderId,
                toScaleId: this.longInfo.toScaleId,
                toVirtualRoomScaleId: this.longInfo.toVirtualRoomScaleId,
                toRoomId: this.longInfo.toRoomId,
                preWaterDegree: this.water,
                waterDegree: this.h_water,
                preGasDegree: this.gas,
                gasDegree: this.h_gas,
                preElecDegree: this.ele,
                elecDegree: this.h_ele
            };
            if (this.lastInfo.elecPrice == 0) {
                delete data.preElecDegree;
                delete data.elecDegree;
            }
            if (this.lastInfo.waterPrice == 0) {
                delete data.preWaterDegree;
                delete data.waterDegree;
            }
            if (this.lastInfo.gasPrice == 0) {
                delete data.preGasDegree;
                delete data.gasDegree;
            }
            this.next(data);
        },
        next(data) {
            PostBusinessOrderroomRoomchange(this.longInfo.orderRoomId, data).done((res) => {
                Toast({
                    message: res.msg,
                    duration: 900
                });
                setTimeout('window.history.go(-2)', 900);
            });
        }
    }
};
</script>
<style scoped>
#big_box {
    background-color: #fff;
}
#big_box > :nth-child(1) {
    color: #000;
    font-weight: bold;
    font-size: 0.8rem;
    text-align: left;
    margin: 8px;
    padding-top: 8px;
}
#top {
    display: flex;
    justify-content: space-around;
    color: #888;
    opacity: 0.7;
    background: #f4f4f4;
    font-size: 0.7rem;
    line-height: 34px;
    border-bottom: 1px solid #efeff4;
}
#center {
    color: #888;
    opacity: 0.7;
    font-size: 0.8rem;
    line-height: 40px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #efeff4;
}
#center > :nth-child(1) {
    width: 30%;
    color: #000;
    font-weight: bold;
}
#center > :nth-child(2) {
    width: 30%;
}
#center > :nth-child(3) {
    width: 30%;
}
.input_box {
    color: #ff7d41;
    width: 50%;
    border: none;
    outline-style: none;
}
#button {
    width: 90%;
    background: #ff7d41;
    opacity: 0.9;
    color: #fff;
    margin-top: 50px;
    font-size: 0.8rem;
    height: 50px;
}
</style>



